* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  html,
  body {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  body {
    width: 100%;
    height: 100%;
    // font-family: $defaultFont;
    background: $lightwhite;
    font-size: rem-calc(18);
    line-height: rem-calc(18);
    user-select: none;
    cursor: move; /* fallback if grab cursor is unsupported */
    cursor: grab;
    cursor: -moz-grab;
    cursor: -webkit-grab;
    &:active{
        cursor: grabbing;
        cursor: -moz-grabbing;
        cursor: -webkit-grabbing;
    }
    #root {
      height: 100%;
      overflow: hidden;
      #bodyWrap {
        width: 100%;
        height:100%;
        position: relative;
        z-index: 1;
      }
      #scrollWrap{
        width:100%;
      }
    }
    i{
      font-style: italic;
    }
    a {
      text-decoration: none;
      display: inline-block;
    }
    span {
      display: inline-block;
    }
    img {
      width: 100%;
      height: auto;
    }
    input,
    textarea,
    button,
    select {
      border: 0;
      padding: 0;
      margin: 0;
      outline: 0;
    }
    input[type="submit"] {
      cursor: pointer;
    }
    [data-src] {
    //   background-color: #eee;
    //   opacity: 0;
    //   visibility: hidden;
      // -webkit-transition: opacity 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
      // transition: opacity 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
  
      &.loaded {
        opacity: 1;
        visibility: visible;
      }
    }
  
    .floatL {
      float: left;
    }
    .floatR {
      float: right;
    }
    .alignC {
      text-align: center;
    }
    .alignL {
      text-align: left;
    }
    .alignR {
      text-align: right;
    }
    .img {
      background-position: center center;
      background-size: cover;
    }
    .cap {
      text-transform: uppercase;
    }
    .center {
      width: 100%;
      padding: 0 rem-calc(74);
    }
    .underline {
      text-decoration: underline;
    }
    .show_on_desktop {
      display: block;
    }
    .show_on_mobile {
      display: none;
    }
  
    ::selection {
      background: #000;
      color: #fff;
    }
    ::-moz-selection {
      background: #000;
      color: #fff;
    }
  
    ::-webkit-input-placeholder {
      color: #000;
    }
    :-moz-placeholder {
      color: #000;
    }
    ::-moz-placeholder {
      color: #000;
    }
    :-ms-input-placeholder {
      color: #000;
    }
  
    #mainWrap {
      &.hide {
        opacity: 0;
        visibility: hidden;
      }
    }
  
    @import "./global/scrollbar";
  }
  
